import React from 'react';
import PhoneArticle from './PhoneArticle';

const Catalog = ({ phonesList, getPhonesList, buyNowHandler, inCart, searchStr }) => {
  getPhonesList();
  
  let filteredPhones = searchStr
    ? phonesList.filter(phone => phone.name.toLowerCase().includes(searchStr))
    : [...phonesList];

  return (
    <div className="phoneCatalog">
      {filteredPhones.map(phone => (
        <PhoneArticle
          key={phone.age}
          phone={phone}
          buyNowHandler={buyNowHandler}
          inCart={inCart}
        />
      ))}
    </div>
  );
}

export default Catalog;
